/**
 * 浏览器版本，只有 HTTP 上报，兼容旧版本包含了插件
 */
import type { FullHttpWeblogInitParams } from '@/types/configuration';
import '../util/polyfill';
import BaseWeblogger from '../platform/browser';
import { BasePlugin } from '../plugins/base';
// plugins
import CrashMonitor from '../plugins/crashMonitor';
import FpsMonitor from '../plugins/fpsMonitor';
import TimingMonitor from '../plugins/timingMonitor';
import Radar from '../plugins/radar/index';
import AutoPV from '../plugins/autoPV';
import { addClass, initSilence } from '@/util/namespace';

export type {
    SupportTypes,
    SendConfig,
    PVOptions,
    ShowOptions,
    TaskOptions,
    CustomOptions,
    BaseOptions,
    VideoEventOptions,
} from './types';

export type LoggerConfigByUser = FullHttpWeblogInitParams[0];
export type WeblogInitParams = FullHttpWeblogInitParams;
class Weblog extends BaseWeblogger {
    declare logConfig: FullHttpWeblogInitParams[0];
    constructor(logConfig: FullHttpWeblogInitParams[0], base: FullHttpWeblogInitParams[1]) {
        super(logConfig, base);
        this.initBuildInPlugins();
    }
    initBuildInPlugins() {
        const {
            autoPV, radar, fps, timing, crash, autoPVFilter,
        } = this.logConfig;
        // 这三个插件不再默认开启
        fps && this.plug(FpsMonitor);
        timing && this.plug(TimingMonitor);
        crash && this.plug(CrashMonitor);
        radar && this.plug(Radar, radar);
        if (autoPV || typeof autoPV === 'undefined') {
            this.plug(AutoPV, { autoPVFilter });
        }
    }
}
// 注册到全局对象
addClass(Weblog);
initSilence();

export {
    Weblog,
    BasePlugin,
};

export default Weblog;
